import styles from "./user-forms.module.css";
import {Button, Input, PasswordInput} from "@ya.praktikum/react-developer-burger-ui-components";
import {Link, Navigate, useLocation, useNavigate} from "react-router-dom";
import React, {useState} from "react";
import {ResetPassword} from "../services/user/user-api";

export function ResetPasswordPage() {
    const [password, setPassword] = useState<string>("");
    const [token, setToken] = useState<string>("");
    const [passwordError, setPasswordError] = useState<string>("");
    const [tokenError, setTokenError] = useState<string>("");
    const navigate = useNavigate();
    const { state } = useLocation();

    const clearErrors = () => {
        setPasswordError("");
        setTokenError("");
    }

    const handleSubmit = async (e: React.SyntheticEvent) => {
        e.preventDefault();
        let hasErrors = false;
        if (!password) {
            setPasswordError(" ");
            hasErrors = true;
        }
        if (!token) {
            setTokenError("   ");
            hasErrors = true;
        }
        if(hasErrors) {
            return
        }
        try {
            const result = await ResetPassword(password, token);
            if(!result) {
                setTokenError(",  !");
            }
            else {
                navigate("/login");
            }
        } catch {
            setTokenError(",  !");
        }
    }

    if(!state || !state.email) {
        return <Navigate to="/forgot-password" replace={true} />;
    }

    return (
        <form className={styles.container} onSubmit={handleSubmit}>
            <h1 className="text text_type_main-medium"> </h1>
            <div className={styles.inputContainer}>
                <PasswordInput
                    placeholder="  "
                    onChange={(e) => { setPassword(e.target.value); clearErrors()}}
                    value={password}
                    name="password"
                    size="default"/>
                {!!passwordError && <span className={`${styles.error} text text_type_main-small`}>{passwordError}</span>}
            </div>
            <Input type="text"
                   placeholder="   "
                   onChange={(e) => { setToken(e.target.value); clearErrors()}}
                   value={token}
                   name="code"
                   error={!!tokenError}
                   errorText={tokenError}
                   size={'default'}/>
            <Button type="primary" size="medium" htmlType="submit">
                
            </Button>
            <span className="text text_type_main-small mt-15"> ? <Link to="/login" className={styles.link}></Link></span>
        </form>
    );
}