import styles from "./user-forms.module.css";
import {Button, EmailInput} from "@ya.praktikum/react-developer-burger-ui-components";
import {Link, useNavigate} from "react-router-dom";
import React, { useState } from "react";
import {StartResetPassword} from "../services/user/user-api";

export function ForgotPasswordPage() {
    const [email, setEmail] = useState<string>("");
    const [error, setError] = useState<string>("");
    const navigate = useNavigate();

    const handleSubmit = async (e: React.SyntheticEvent) => {
        e.preventDefault();
        if (!email) {
            setError(" e-mail")
            return;
        }
        try {
            const result = await StartResetPassword(email);
            if(!result) {
                setError("   e-mail  ");
            }
            else {
                navigate("/reset-password", {state: {email}});
            }
        } catch {
            setError(",  !");
        }
    }

    return (
        <form className={styles.container} onSubmit={handleSubmit}>
            <h1 className="text text_type_main-medium"> </h1>
            <div className={styles.inputContainer}>
                <EmailInput
                    placeholder="E-mail"
                    onChange={(e) => { setEmail(e.target.value); setError("")}}
                    value={email}
                    name="email"
                    size="default" />
                {!!error && <span className={`${styles.error} text text_type_main-small`}>{error}</span>}
            </div>
            <Button type="primary" size="medium" htmlType="submit">
                
            </Button>
            <span className="text text_type_main-small mt-15"> ? <Link to="/login" className={styles.link}></Link></span>
        </form>
    );
}
